.muster-list {
  width: 75%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.muster-box{
  width: 350px; 
  padding: 5px;
  margin: 10px;
  
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #282c34;
  box-shadow: 3px 3px rgb(0 0 0 / 60%);
}
.muster-title {
  margin: .2em;
  font-family: 'Open Sans', 'Source Han Sans SC', 'Noto Sans CJK SC', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', sans-serif;
  color: #282c34;
  font-size: 1.3em;
  font-weight: bold;
  box-shadow: 0px 1px 0 0 rgb(34 36 38 / 60%);

  display: flex;
  flex-direction: row;
}

.muster-people-list {
  margin: .2em 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.muster-person-display {
  color: #282c34;
  margin: 2px;
  padding: 0 2px 2px 2px;

  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: #282c34;
}

.muster-person-edit-form-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.muster-person-edit-input {
  width: 8em;
}

.muster-person-edit-submit-btn {
  color: #282c34;
  margin: 2px;
  padding: 0 2px;
  cursor: pointer;
  font-size: 1.1em;

  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #282c34;
}

.add-muster {
  width: 350px; 
  padding: 5px;
  margin: 10px;
  
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #282c34;
  box-shadow: 3px 3px rgb(0 0 0 / 60%);

  font-family: 'Open Sans', 'Source Han Sans SC', 'Noto Sans CJK SC', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', sans-serif;
  color: #282c34;
  font-size: 1.1em;
  font-weight: bold;
  text-align: center;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.add-muster-edit-form-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.add-muster-edit-input {
  width: 8em;
}

.add-muster-edit-submit-btn {
  color: #282c34;
  margin: 2px;
  padding: 0 2px;
  cursor: pointer;
  font-size: 1.1em;

  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #282c34;
}


.muster-del-popup {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.muster-del-popup-submit {
  margin: 20px;
  padding: 5px;
  font-size: 1.5em;
	text-align: center;
	color: red;
	user-select: none;

	border: 2px solid red;
	border-radius: 5px;
	background-color: #1f1f1f;
}

.muster-del-popup-desc {
  color: red;
}

.muster-del-btn {
  margin: 0 5px 0 0;
  width: 1em;
  height: 1em;
  color: red;
  
